import React, { useEffect, useState } from "react";
import classNames from "classnames/bind";
import style from "./StateButton.module.css";
import { useDispatch, useSelector } from "react-redux";
import { UpdatePstatusAction, UpdateStatusAction } from "redux/inspection_Reducer";
import Swal from "sweetalert2";
import { UpdateInspectStatus, UpdatePatientStatus } from "apis/inspection";

const cx = classNames.bind(style);

export const StateButton = (props) => {
  const buttonName = props.value;
  //,,
  const changeValue = props.change;
  const dcheck = props.checkfun;
  const checkList = useSelector((state) => state.inspectReducer.checked);
  const dispatch = useDispatch();
  const patient = useSelector((state) => state.inspectReducer.patient);
  const inspectList = props.list;

  const updateInspect = async (checkList, changeValue) => {
    await UpdateInspectStatus(checkList, changeValue);
  };

  const patientInspect = async (dId, totalIstatus) => {
    const diagnosis = {
      dId,
      totalIstatus,
    };
    await UpdatePatientStatus(diagnosis);
  };

  const changeState = (props) => {
    if (checkList.length === 0) {
      Swal.fire({
        icon: "error",
        text: " ",
        confirmButtonColor: "#3085d6",
      });
      return;
    }
    // 
    if (patient.tstatus === "") {
      Swal.fire({
        icon: "error",
        text: " ",
        confirmButtonColor: "#3085d6",
      });
      return;
    }
    if (patient.tstatus === "") {
      if (changeValue === "") {
        Swal.fire({
          icon: "error",
          text: " .",
          confirmButtonColor: "#3085d6",
        });
        return;
      }
    }

    if (checkList.length > 1) {
      console.log("");
      if (changeValue === "") {
        Swal.fire({
          icon: "error",
          text: "  .",
          confirmButtonColor: "#3085d6",
        });
        return;
      }
      if (changeValue === "") {
        Swal.fire({
          icon: "error",
          text: "  .",
          confirmButtonColor: "#3085d6",
        });
        return;
      }
    }

    for (let i = 0; i < inspectList?.length; i++) {
      if (inspectList[i].iStatus === "") {
        if (changeValue === "") {
          Swal.fire({
            icon: "error",
            text: " .",
            confirmButtonColor: "#3085d6",
          });
          return;
        }
      }
    }
    for (let i = 0; i < checkList?.length; i++) {
      if (checkList[i].iStatus === "") {
        if (changeValue === "" || changeValue === "") {
          Swal.fire({
            icon: "error",
            text: " ",
            confirmButtonColor: "#3085d6",
          });
          return;
        }
        if (changeValue === "") {
          Swal.fire({
            icon: "error",
            text: "  .",
            confirmButtonColor: "#3085d6",
          });
          return;
        }
      }
      if (checkList[i].iStatus === "") {
        if (changeValue === "" || changeValue === "" || changeValue === "") {
          Swal.fire({
            icon: "error",
            text: " ",
            confirmButtonColor: "#3085d6",
          });
          return;
        }
        if (changeValue === "") {
          Swal.fire({
            icon: "error",
            text: "  .",
            confirmButtonColor: "#3085d6",
          });
          return;
        }
      }
      if (checkList[i].iStatus === "") {
        if (changeValue === "") {
          Swal.fire({
            icon: "error",
            text: "  .",
            confirmButtonColor: "#3085d6",
          });
          return;
        }
        if(changeValue===""){
          if(checkList[i]?.iResult===null){
            Swal.fire({
              icon: "error",
              text: "  .",
              confirmButtonColor: "#3085d6",
            });
            return;
          }
        }
        if(changeValue===""){
          if(checkList[i]?.iResult!==null){
            Swal.fire({
              icon: "error",
              text: "    .",
              confirmButtonColor: "#3085d6",
            });
            return;
          }
        }
      }
      //Modal
      if (changeValue === "") {
        props.openModal();
      }
      if (changeValue === "") {
        props.openModal2();
        return;
      }
    }

    updateInspect(checkList, changeValue);

    //      
    dispatch(UpdateStatusAction());
    for (let i = 0; i < inspectList?.length; i++) {
      if (checkList[0]?.iId === inspectList[i]?.iId && checkList[0]?.bundleCode === inspectList[i]?.bundleCode) {
        inspectList[i].iStatus = changeValue;
      }
    }

    //props    
    dcheck();

    // tstatus 
    const arr = [];
    const arr2 = [];
    let value = "";
    for (let i = 0; i < inspectList?.length; i++) {
      if (inspectList[i].iStatus === "") {
        arr.push(inspectList[i]);
      } else if (inspectList[i].iStatus === "") {
        value = "";
        patientInspect(inspectList[i].dId, value);

        dispatch(UpdatePstatusAction(value));
      } else if (inspectList[i].iStatus === "") {
        arr2.push(inspectList[i]);
      }
    }

    if (arr.length && arr.length === inspectList.length) {
      patientInspect(inspectList[0].dId, "");
      value = "";
      dispatch(UpdatePstatusAction(value));
    }
    if (arr2.length && arr2.length === inspectList.length) {
      patientInspect(inspectList[0].dId, "");
      value = "";
      dispatch(UpdatePstatusAction(value));
    }
  };

  return (
    <div>
      <input type="button" className={cx(style.stateButton)} value={buttonName} onClick={() => changeState(props)} />
    </div>
  );
};
