//  
const initialState = { //    
  patient_id: ''
};

//  
const SET_PATIENT = "patient_id/setPatient"; //    

//   
export const createSetManagePatient = (patient_id) => {  //   ? 
  return {type:SET_PATIENT, patient_id} //   
};

// 
const managePatientReducer = (state=initialState, action) => {//  initialState( ) 
  if(action.type === SET_PATIENT) {
    return {...state, patient_id: action.patient_id};
  }else {
    return state;
  }
};

export default managePatientReducer;