import moment from "moment";

//  
const initialState = { //    
  day: moment(new Date()).format('YYYY-MM-DD'),
  Rday: moment(new Date()).day()
};

//  
const SET_DAY = "reception/setDay"; //    
const SET_RDAY = "reception/setRDay"
//   
export const setReceptionDay = (day) => { //   ? 
  return {type:SET_DAY, day}//   
}; 
export const setRday = (Rday) => { //   ? 
  return {type:SET_RDAY, Rday}//   
}; 


// 
const receptionReducer = (state=initialState, action) => {//  initialState( ) 
  if(action.type === SET_DAY) {
    return {...state, day: action.day};
  }else if(action.type === SET_RDAY) {
    return {...state, Rday: action.Rday};
  } 
  else {
    return state;
  }
};

export default receptionReducer;