import React from 'react'
import classNames from "classnames/bind";
import style from "./Progress.module.css";
import { useSelector } from 'react-redux';


const cx =  classNames.bind(style);

function Progress(props){

    const user = props.data;
    const inspectList = props.list;
   
    //     
    const completeList=inspectList.filter((value)=>value.iStatus==='');
    useSelector(state => state.inspectReducer.checked);
    // 
    let progress = Math.floor((completeList.length/inspectList.length)*100 );
    //progressBar style     progress %   
    let percent = String(progress).concat('%');

    //progress  undefined 
    if(!progress){
        progress=0;
        percent=0;
    }

    const viewDate = (str) =>{
         console.log(str)
         if(str===undefined){
             return
         }
         const strArr=str.split(" ")
         const hour =strArr[1].substr(0,5)
         return hour 
       }
    
    //  
    const proceeding = inspectList.find((value)=>value.iStatus==='')
    console.log(proceeding)
    return (
        <div>
        <div className={cx(style.middle_right_top)}>
          <div className={cx(style.patientProgress)}>   
                    <div className="progress-title m-2" style={{fontSize: '18px', fontWeight:'bold'}}> </div>
                        <div className="progress rounded-pill" style={{width:'100%',height: '25px', marginRight: '0'}}>
                            <div className="progress-bar progress-bar-striped bg-success progress-bar-animated" role="progressbar" style={{ width:percent }}>{progress}%</div>
                        </div>
                        <div className={cx(style.progressStatus)}>
                            <ul>
                                <li>  : {inspectList.length} </li>
                                <li> : {completeList.length}</li>
                            </ul>
                        </div>
                        <div className={cx(style.inspectName)}>
                            <div>  </div>
                            <div style={{display:'flex',justifyContent:'space-between'}}>
                            {proceeding!==undefined?<div> {proceeding.iName}</div>:<div>   .</div>}
                            {proceeding!==undefined? <div>   : {proceeding?.iResult!==null?"O":"X"} </div>: <></>}
                            </div>
                        </div>
                        <div className = {cx(style.limit)}>
                            {proceeding!==undefined?
                            <ul>
                                <li> : {proceeding.lowerLimit}</li>
                                <li> : {proceeding.upperLimit}</li>
                            </ul>:
                            <></>
                            }
                        </div>
                </div>
                <div className={cx(style.percentData)}>
                    <div className={cx(style.progress_percent)}>{progress}%</div>
                </div>
                <div className={cx(style.patientInfo)}>
                    <div className={cx(style.info,'mt-3')}>
                        <div className="ml-3 mb-1"> : {user?.patientId} </div>
                        <div className="ml-3 mb-1">: {user?.patientName}</div>
                        <div className="ml-3 mb-1"> : {user?.patientSex}</div>
                        <div className="ml-3 mb-1"> : {user?.patientAge}</div>
                        <div className="ml-3 mb-1"> : {viewDate(user?.iDate)}</div>
                      </div>
            </div>
        </div>
    </div>
    )
}

export default Progress
