import style from "./main.module.css";
import MainNotice from "./MainNotice/MainNotice";
import photo1 from 'images/1.png';
import photo2 from 'images/2.png';
import photo3 from 'images/3.png';
import { Link } from "react-router-dom"

function Main(props) {
    return (
      <div>
        <div className={style.mbody}>
          <div className={style.titleContainer}>
            <div className={style.title}>WEHAGO-H</div>
          </div>
          <div className={style.contentBox}>
            <div className={style.notice}>
              <div className={style.header}>NOTICE</div>
                <div style={{height:'41.5vh',overflow:'auto'}}><MainNotice/></div>
            </div>
            <div className={style.rightBox}>
            <div className={style.priceheader}>PRICE</div>
              <div className={style.topBox}>
                <div style={{marginTop:'40px', fontSize:'25px'}}>    6,000</div>
                <div style={{fontSize:'13px'}}>  30.000 ()</div>
              </div>
              <div className={style.middleBox}>
                <div className={style.innerBox}>
                  <div style={{textAlign:'center', fontSize:'17px'}}>    , /     .</div>
                </div>
              </div>
              <div className={style.bottomBox}>
                <div style={{marginRight:'20px'}}><img style={{width:'50px'}} src={photo1} alt="photo1"/><b> / </b></div>
                <div style={{marginRight:'20px'}}><img style={{width:'40px'}} src={photo3} alt="photo3"/><b></b></div>
                <div><img style={{width:'47px'}} src={photo2} alt="photo2"/><b> / </b></div>
              </div>
            </div>
          </div>
          <div className={style.loginBox}>
          <Link to="/hlogin" style={{textDecoration:'none', color:'black'}}><div className={style.loginButton}>LOGIN</div></Link> 
          </div>      
        </div>
      </div>
    )
}

export default Main;
